.header{
  height: 9rem;
  padding: 2%;
  background-image:linear-gradient(90.03deg, #11172C 36.91%, rgba(17, 23, 44, 0.1))
  ,url('../../assets/aws-bg.jpg');
  background-position: right center;
  color: rgba(73, 195, 222, 1);
 
}
.picture_container{
    display: flex;
    flex-direction: row;
    margin-bottom: 1rem;
    align-items: center;
   color: white; 
  
}
.picture_container h1{
    font-weight: 100;
}

.picture_container img {
    height: 3rem;
    object-fit: contain;
    margin-right: 15px;
    margin-left: 15px;
}

@media screen and (max-width: 700px) {
	.header {
		height: 6rem;
	}
    .picture_container img {
        height: 2rem;
        object-fit: contain;
        margin-right: 10px;
        margin-left: 5px;
    }
    .header h2{
        font-size: 1rem;
    }
}