.navigation {
    display: flex;
    width: 100%;
    height: calc(100vh - 20rem);
    flex: 0.2;
    flex-direction: column;
    padding: 1rem 0;
    border-radius: 0px;
    background: white;
    border-right: 1px solid rgba(200, 200, 200, 0.3);
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
    overflow-y: auto;
}

.navigation::-webkit-scrollbar {
    width: 5px;
}

.navigation::-webkit-scrollbar-track {
    background-color: #49C3DE;
}

.navigation::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgba(255, 255, 255, 1);
}

.profile {
    width: 100%;
    height: 8rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
}

.dp {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid whitesmoke;
    margin-bottom: 0.5rem;
}

.dp img {
    width: 100%;
    object-fit: cover;
}

.links {
    display: flex;
    flex-direction: column;
    padding: 1rem 2rem;
}

.links ul {
    display: flex;
    list-style: none;
    /* justify-content: space-around; */
    width: 100%;
    flex-direction: column;
}

.links ul li {
    display: flex;
    flex-direction: column;
    list-style: none;
    justify-content: center;
    /* height: 3rem; */
    cursor: pointer;
    padding: 1rem;
    /* align-items: center; */
    margin-bottom: 0.5rem;
    border-radius: 5px;
    background: #fafafa;
    border: 1px solid gainsboro;
    margin-bottom: 0.5rem;
}

.links ul li a {
    display: flex;
    list-style: none;
    /* justify-content: space-between; */
    text-decoration: none;
    align-items: center;
    text-decoration: none;
    color: black;
    font-size: 13px;
}

.links ul li span {
    display: flex;
    list-style: none;
    /* justify-content: space-between; */
    text-decoration: none;
    align-items: center;
    text-decoration: none;
    color: black;
    font-size: 13px;
}

.links ul li a svg {
    margin-right: 0.5rem;
}

.links ul li span svg {
    margin-right: 0.5rem;
}

.links p {
    color: white;
}

.links ul li:hover {
    background: #49C3DE;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.active {
    border: 1px solid black !important;
    background: #49C3DE !important;
    color: black;
    /* //   box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
}

.links ul .active a {
    color: white;
}

.links ul .active span {
    color: white;
}

/* end of mobile component */
.mobileNav {
    display: none;
    justify-content: space-between;
    background: #fff;
    max-height: 5rem;
    padding: 0.5rem 1rem;
    align-items: center;
    color: black;
}

.bars {
    font-size: 1.5rem;
}

.mobileNavigation {
    display: flex;
    background: white;
    flex-direction: column;
    padding: 0.5rem 1rem;
    height: 100vh;
    overflow-y: auto;
}

/* mobile component */

