.loginContainer {
    display: flex;
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
}

.bgContainer {
    display: flex;
    flex: 0.7;
    height: 100vh;
    position: relative;
    overflow: hidden;
    flex-direction: column;
}

.overlay {
    width: 100%;
    display: flex;
    height: 100vh;
    flex-direction: column;
    color: white;
    background: linear-gradient(50deg, rgba(73,
                195,
                222, 0.7), rgba(0, 0, 0, 0));
    position: absolute;
    justify-content: center;
    padding: 5rem;
}

.bgImages {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    flex: 1;
    background: url(../assets/bg1.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    animation: slideBg 20s linear infinite 8s;
    animation-timing-function: ease-in-out;
    background-repeat: no-repeat;
}

@keyframes slideBg {
    0% {
        background: url(../assets/bg1.jpg);
        background-size: cover;
    }

    25% {
        background: url(../assets/bg2.jpeg);
        background-size: cover;
    }

    50% {
        background: url(../assets/bg3.jpeg);
        background-size: cover;
    }

    75% {
        background: url(../assets/bg4.jpg);
        background-size: cover;
    }

    100% {
        background: url(../assets/bg5.jpeg);
        background-size: cover;
    }
}

.loginFormContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 0.3;
    height: 100vh;
    padding: 1rem;
    background: linear-gradient(0deg,
            rgba(255, 255, 255, 0.7),
            rgba(255, 255, 255, 0.8)),
        url("../assets/spec.avif");
}

