.overlay img {
    max-height: 5rem;
    object-fit: contain;
}

.formButtonContainer {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 2rem;
}

.formButtonContainer span {
    font-size: 12px;
    text-align: right;
}

.formButtonContainer span a {
    text-decoration: none;
    color: var(--green);
}

.formPadding h1 {
    color: var(--green);
}

.formPadding {
    padding: 1rem 2rem;
}

.center {
    justify-content: center;
}