.footer_container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 5rem;
    padding: 2% 5%;
    background: rgba(17, 23, 44, 1);
    color: white;
}

.footer_container a{
    color: white;
    text-decoration: none;
}

.footer_container ul{
    display: flex;
    flex-direction: row;
    list-style: none;
    margin: 0;
    padding: 0;
}

.footer_container ul li{
    margin-right: 1.5rem;
}

.footer_container ul li a{
    color: white;
}

@media screen and (max-width: 700px) {
	.footer_container{
       
        padding: 2% 4%;
        
    }

    .footer_container a{
       
       font-size: .8rem;
        
    }
    .footer_container ul li{
        margin-right: .5rem;
    }
}