.card {
  display: flex;
  width: 100%;
  flex-basis: 30%;
  padding: 2rem;
  border-radius: 10px;
  background: white;
  box-shadow: 0px 4px 60px 0px #e5e5e5;
  text-decoration: none;
  color: black;
  justify-content: space-between;
  align-items: center;
}

p {
  font-size: 14px;
  margin-bottom: 1rem;
}

.text {
  display: flex;
  flex: 0.7;
  flex-direction: column;

  h1 {
    font-size: 2rem;
  }
}

.circle {
  display: flex;
  width: 80px;
  height: 80px;
  background: rgba(73,
      195,
      222, 0.3);
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
  margin-top: 1rem;
}

.icircle {
  display: flex;
  width: 65px;
  height: 65px;
  background: #49C3DE;
  ;
  justify-content: center;
  align-items: center;
  border-radius: 50%;

  svg {
    font-size: 2rem;
  }
}


/* Media Query */

@media screen and (min-width: 1300px) and (max-width: 1600px) {
  .card {
    flex-basis: 30%;
  }
}

@media screen and (max-width: 1300px) {
  .card {
    flex-basis: 30%;
  }
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
  .card {
    flex-basis: 47%;
  }
}

@media screen and (max-width: 1050px) {
  .card {
    flex-basis: 47%;
  }
}

@media screen and (max-width: 1024px) {
  .card {
    flex-basis: 47%;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .card {
    flex-basis: 100%;
  }
}

@media screen and (max-width: 700px) {
  .card {
    flex-basis: 100%;
  }
}

@media screen and (max-width: 550px) {
  .card {
    flex-basis: 100%;
  }
}