@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap");
@import url("./shared/button.css");
@import url("./shared/auth.css");


* {
  font-family: "Montserrat", sans-serif !important;
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

.form_container select {
  height: 3rem;
  padding: .8rem 1rem;
  border-right: 16px solid transparent;
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.29);
  border-radius: 15px;
  outline: none;
  border-radius: 5px;
  width: 100%;
  outline: none;
  background: white;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  background-size: 1em;
}

.appContainer {
  display: flex;
  flex: 1;
  width: 100%;
  height: calc(100vh - 10rem);
  justify-content: space-around;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.8;
  height: calc(100vh - 10rem);
  background: whitesmoke;
  /* justify-content: space-between; */
}

.cardFlex {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.pageContents {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
}

.mainContainer {
  width: 100%;
  display: flex;

}

.leftContainer {
  display: flex;
  flex: 0.6;
  padding: 2rem;
  flex-direction: column;
}

.rightContainer {
  display: flex;
  flex-direction: column;
  flex: 0.4;
  padding: 2rem;
}

.leftContainer ul,
ol {
  padding: 1rem 2rem;
  line-height: 1.5rem;
  font-size: 14px;
}

.leftContainer h3,
h2,
h1 {
  margin-bottom: 1rem;
}

.captchaContainer {
  padding: 1rem 2rem;
}

.leftContainer p {
  font-size: 14px;
}

.titleContainer {
  display: flex;
  padding: 2rem;
}


