@media screen and (max-width: 1024px) {
  .mainContainer {
    flex-direction: column;

  }

  .appContainer {
    flex-direction: column;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .mainContainer {
    flex-direction: column;

  }

  .appContainer {
    flex-direction: column;
  }
}

@media screen and (max-width: 700px) {
  .mainContainer {
    flex-direction: column;

  }

  .appContainer {
    flex-direction: column;
  }
}

@media screen and (max-width: 550px) {
  .mainContainer {
    flex-direction: column;

  }

  .appContainer {
    flex-direction: column;
  }
}